import { createClient } from "@/lib/supabase/server"
import { redirect } from 'next/navigation'
import { BookingList } from "@/components/booking-list"
import { BookingsCalendar } from "@/components/bookings-calendar"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { ArrowLeft, List, Calendar } from 'lucide-react'

export default async function ShopBookingsPage() {
  const supabase = await createClient()

  const {
    data: { user },
  } = await supabase.auth.getUser()

  if (!user) {
    redirect("/auth/login")
  }

  const { data: shop } = await supabase.from("repair_shops").select("*").eq("owner_id", user.id).maybeSingle()

  if (!shop) {
    return (
      <div className="container mx-auto max-w-4xl px-4 py-8">
        <Card>
          <CardContent className="py-8 text-center">
            <p className="mb-4 text-muted-foreground">You need to set up your shop first</p>
            <Button asChild>
              <Link href="/shop/settings">Set Up Shop</Link>
            </Button>
          </CardContent>
        </Card>
      </div>
    )
  }

  const { data: bookings = [] } = await supabase
    .from("bookings")
    .select("*")
    .eq("shop_id", shop.id)
    .order("appointment_date", { ascending: true })
    .order("appointment_time", { ascending: true })

  const pendingCount = bookings.filter((b) => b.status === "pending").length
  const confirmedCount = bookings.filter((b) => b.status === "confirmed").length
  const completedCount = bookings.filter((b) => b.status === "completed").length

  return (
    <div className="container mx-auto max-w-7xl px-4 py-8">
      <Link href="/shop/dashboard" className="mb-6 inline-flex items-center gap-2 text-sm text-primary hover:underline">
        <ArrowLeft className="h-4 w-4" />
        Back to Dashboard
      </Link>

      <div className="mb-8">
        <h1 className="mb-2 text-3xl font-bold">Bookings</h1>
        <p className="text-muted-foreground">Manage your appointment bookings</p>
      </div>

      {/* Stats */}
      <div className="mb-8 grid gap-4 md:grid-cols-4">
        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">Total Bookings</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{bookings.length}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">Pending</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-yellow-600">{pendingCount}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">Confirmed</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{confirmedCount}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">Completed</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{completedCount}</div>
          </CardContent>
        </Card>
      </div>

      <Tabs defaultValue="calendar" className="space-y-4">
        <TabsList>
          <TabsTrigger value="calendar" className="flex items-center gap-2">
            <Calendar className="h-4 w-4" />
            Calendar View
          </TabsTrigger>
          <TabsTrigger value="list" className="flex items-center gap-2">
            <List className="h-4 w-4" />
            List View
          </TabsTrigger>
        </TabsList>
        
        <TabsContent value="calendar">
          <BookingsCalendar bookings={bookings} />
        </TabsContent>
        
        <TabsContent value="list">
          <BookingList bookings={bookings} isShopOwner={true} />
        </TabsContent>
      </Tabs>
    </div>
  )
}
